import * as React from 'react';
import { css } from 'office-ui-fabric-react';
import strings from '../loc/Strings.resx';
import styles from './PropertyPaneFooter.module.scss';
import { StringHelper } from '@ms/odsp-utilities-bundle';
import { PageDirection } from '../propertyPane/PropertyPane';

export interface IPropertyPaneFooterProps {
  isReactive: boolean;
  isSaveButtonEnabled: boolean;
  numberOfPages: number;
  currentPage: number;
  onSaveClick: () => void;
  onFlipPage: (direction: PageDirection) => void;
}

export default class PropertyPaneFooter extends React.Component<IPropertyPaneFooterProps, void> {

  constructor(props: IPropertyPaneFooterProps) {
    super(props);

    this._renderCommandButtons = this._renderCommandButtons.bind(this);
    this._pageNavigationButtons = this._pageNavigationButtons.bind(this);
  }

  public render(): React.ReactElement<{}> {
    return ((!this.props.isReactive || this.props.numberOfPages > 1) && (
        <div className={ styles.propertyPaneFooter }>
          { this._renderCommandButtons() }
          { this._pageNavigationButtons() }
        </div>
      ));
  }

  private _renderCommandButtons(): React.ReactElement<{}> {
    // We are specifically checking for 'false' because there are cases where the prop can have
    // 'undefined' as it's value and we do not want to display the command buttons in that case.
    return ((this.props.isReactive === false) && (
        <div className={ this.props.numberOfPages > 1 ? styles.propertyPaneCommandButtonsBox : undefined }>
          <button className='ms-Button ms-Button--primary' disabled={ !this.props.isSaveButtonEnabled }
            onClick={ this.props.onSaveClick }>
            <span className='ms-Button-label'>{ strings.Save }</span>
          </button>
        </div>
      ));
  }

  private _pageNavigationButtons(): React.ReactElement<{}> {
    return ( this.props.numberOfPages > 1 && (
      <div className={ styles.propertyPanePageNav }>
        <button
          className={ styles.propertyPaneBackNavLink }
          disabled={ this.props.currentPage === 1 }
          onClick={ () => { this.props.onFlipPage(PageDirection.back); } }>
          <i className={ css(
            this._arrowKeyIcon(PageDirection.back),
            styles.propertyPaneBackArrow) }></i>
          { strings.PropertyPaneBackPage }
        </button>
        <span role='status' className={ css(styles.propertyPanePageOfPages) }>
          { StringHelper.format(
            strings.PropertyPanePageOfPages,
            this.props.currentPage,
            this.props.numberOfPages
          ) }
        </span>
        <button
          className={ styles.propertyPaneNextNavLink }
          disabled={ this.props.currentPage === this.props.numberOfPages }
          onClick={ () => { this.props.onFlipPage(PageDirection.next); } }>
          { strings.PropertyPaneNextPage }
          <i className={ css(
            this._arrowKeyIcon(PageDirection.next),
            styles.propertyPaneNextArrow) }></i>
        </button>
      </div>
    ));
  }

  private _arrowKeyIcon(dir: PageDirection): string {
    const rtl: boolean = document.dir === 'rtl';
    const iconCSS: string = 'ms-Icon ms-Icon--Chevron';

    if (dir === PageDirection.back) {
      return iconCSS + (rtl ? 'Right' : 'Left');
    } else {
      return iconCSS + (rtl ? 'Left' : 'Right');
    }
  }
}



