import * as React from 'react';
import * as ReactDOM from 'react-dom';

import { IPropertyPaneCustomFieldProps } from './IPropertyPaneCustomField';

/* tslint:disable:no-any */
export default class PropertyPaneCustomFieldHost extends React.Component<IPropertyPaneCustomFieldProps, any> {
/* tslint:enable:no-any */
  private _domElement: HTMLElement;

  public componentDidMount(): void {
    const domNode: HTMLElement = ReactDOM.findDOMNode(this) as HTMLElement;
    this.props.onRender(domNode);
  }

  public componentWillUnmount(): void {
    if (this.props.onDispose) {
      this.props.onDispose(this._domElement);
    }
  }

  public render(): React.ReactElement<IPropertyPaneCustomFieldProps> {
    return (
      <div className={ 'ms-CustomFieldHost' }></div>
    );
  }
}



